export const getStation = (entities, id) => entities.Stations[id];
export const getSlot = (entities, id) => entities.Slots[id];
export const getClinic = (entities, id) => {
  const clinic = entities.ClinicsInfo[id];
  return {
    ...clinic,
    StationsDistance: clinic.StationsDistance.map(dist => ({
      ...dist,
      Station: getStation(entities, dist.Station)
    }))
  };
};
export const getSpeciality = (entities, id) => entities.Specialities[id];
export const getDoctor = (entities, id) => {
  const doctor = entities.Doctors[id];
  return {
    ...doctor,
    Clinics: doctor.Clinics.map(clinicId => ({
      ...getClinic(entities, clinicId),
      Slots:
        doctor.Slots &&
        doctor.Slots[clinicId] &&
        doctor.Slots[clinicId].map(slotId => getSlot(entities, slotId))
    })),
    Specialities: doctor.Specialities.map(specId =>
      getSpeciality(entities, specId)
    )
  };
};
export const getCity = (state, id) =>
  state.entities.cities.find(city => city.Id === id);
export const getSearchParams = state => ({
  ...state.form.search.values,
  city: getCity(state, state.form.search.values.city.Id)
});
export const getSearch = state => state.entities.search;
