import React from 'react';
import PropTypes from 'prop-types';
import { Button } from '@vkontakte/vkui';

import styles from './Clinic.module.css';
import SlotButtons from '../atoms/Slots';
import StationDistance from '../atoms/StationDistance';
import { ReactComponent as Icon32Map } from '../../svg/Icon32Map.svg';

const Clinic = ({ clinic: { Street, House, StationsDistance, Slots } }) => (
  <div className={styles.Clinic}>
    <div className={styles.Location}>
      <div>
        <div className={styles.Address}>{`${Street}, ${House}`}</div>
        <div className={styles.Stations}>
          {StationsDistance &&
            StationsDistance.map(distance => (
              <StationDistance key={distance.Station.Id} distance={distance} />
            ))}
        </div>
      </div>
      <Icon32Map />
    </div>
    <div className={styles.Order}>
      {Slots ? (
        <SlotButtons slots={Slots} />
      ) : (
        <div>
          <div className={styles.OrderButton}>
            <Button level="commerce" stretched>
              Записаться
            </Button>
          </div>
          <div className={styles.OrderDescription}>
            В этой клинике у врача нет онлайн-расписания.
            <br />
            Оставьте заявку и мы перезвоним в течении 10 минут.
          </div>
        </div>
      )}
    </div>
  </div>
);

Clinic.propTypes = {
  clinic: PropTypes.shape({
    Street: PropTypes.string.isRequired,
    House: PropTypes.string.isRequired,
    Slots: PropTypes.array,
    StationsDistance: PropTypes.array
  }).isRequired
};

export default Clinic;
