import { connect } from 'react-redux';
import { normalize } from 'normalizr';
import moment from 'moment';

import Search from '../../components/panels/Search';
import { search as searchSchema } from '../../schema';
import { getDoctor, getSearch, getSearchParams } from '../../selectors';

// TODO: Добавить станции метро
const makeDesciption = ({ date, city }) => {
  const dateDescription = moment(date)
    .locale('ru')
    .calendar(null, {
      sameDay: '[cегодня]',
      nextDay: '[завтра]',
      nextWeek: 'Do MMMM',
      sameElse: 'Do MMMM'
    });
  const cityDescription = `в городе ${city.Name}`;
  return [dateDescription, cityDescription].join(', ');
};

const mapStateToProps = (state, ownProps) => {
  const { entities, result } = normalize(
    getSearch(state).results,
    searchSchema
  );
  return {
    ...ownProps,
    search: {
      title: getSearchParams(state).speciality.NamePlural,
      isLoading: getSearch(state).isLoading,
      results: result.map(id => getDoctor(entities, id)),
      description: makeDesciption(getSearchParams(state))
    }
  };
};

export default connect(mapStateToProps)(Search);
