import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, Spinner, HeaderButton } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';

import styles from './Search.module.css';
import DoctorCard from '../organisms/DoctorCard';

const Search = ({
  id,
  search: { title, results, isLoading, description },
  onBack
}) => (
  <Panel id={id} className={styles.Search}>
    <PanelHeader
      left={
        <HeaderButton onClick={onBack}>
          <Icon28ChevronBack />
        </HeaderButton>
      }
    >
      {title}
    </PanelHeader>
    <div className={styles.Description}>
      Расписание варчей на <span>{description}</span>
    </div>
    <div className={styles.Results}>
      {results.map(doctor => (
        <div className={styles.Doctor} key={doctor.Id}>
          <DoctorCard className={styles.DoctorCard} doctor={doctor} />
        </div>
      ))}
    </div>
    {isLoading && <Spinner />}
  </Panel>
);

Search.propTypes = {
  id: PropTypes.string.isRequired,
  onBack: PropTypes.func.isRequired,
  search: PropTypes.shape({
    title: PropTypes.string,
    results: PropTypes.arrayOf(PropTypes.object).isRequired,
    isLoading: PropTypes.bool.isRequired,
    description: PropTypes.string.isRequired
  }).isRequired
};

export default Search;
